.jp-action-button {
	&--button {
		// Allow color to be overridden by the .is-primary class from the <Button /> component
		background: #000;
	}

	&--button,
	&--button.components-button {
		border-radius: 4px;
		font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen-Sans', 'Ubuntu', 'Cantarell', 'Helvetica Neue', sans-serif;
		font-style: normal;
		font-weight: 600;
		font-size: 14px;
		line-height: 18px;
		text-align: center;
		min-width: 264px;
		height: 40px;
		display: block;
	}

	&__error{
		color: var( --jp-red ) !important;
		line-height: 25px !important;
		padding-left: 25px;
		background: url("data:image/svg+xml;base64,") no-repeat 0 0;
	}
}
